import { ErrorHandler, NgModule } from '@angular/core';
import { Http, HttpModule } from '@angular/http';
import { BrowserModule } from '@angular/platform-browser';
import { Camera } from '@ionic-native/camera';
import { GoogleMaps } from '@ionic-native/google-maps';
import { SplashScreen } from '@ionic-native/splash-screen';
import { Geolocation } from '@ionic-native/geolocation';
import { StatusBar } from '@ionic-native/status-bar';
import { IonicStorageModule, Storage } from '@ionic/storage';
import { TranslateLoader, TranslateModule } from '@ngx-translate/core';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';
import { IonicApp, IonicErrorHandler, IonicModule } from 'ionic-angular';

import { Items } from '../mocks/providers/items';
import { Settings } from '../providers/providers';
import { User } from '../providers/providers';
import { Api } from '../providers/providers';
import { MyApp } from './app.component';
// Configuration Firebase
import { FacebookModule } from 'ngx-facebook';
//Modulos Firebase
import { AngularFireModule } from 'angularfire2';
import { AngularFireDatabaseModule } from 'angularfire2/database';
import { AngularFireAuthModule } from 'angularfire2/auth';
import { DropdownService } from "../services/dropdown.service";
import { ConsultaCepService } from "../services/consulta-cep.service";
import { MaskShared } from "../shared/masks";
import { NativeGeocoder } from "@ionic-native/native-geocoder";
import { GoogleApis } from "../services/consulta-google-apis";
import { AuthServiceProvider } from "../providers/auth-service";
import { Facebook } from "@ionic-native/facebook";
import { UsuarioService, DistribuidorService, MarcaService } from "../providers/database/database-providers";
import { DirectivesModule } from '../directives/directives.module';
import { ImageCropperModule, ImageCropperComponent } from "ng2-img-cropper";
import { PhotoProvider } from '../providers/photo/photo';
import { PerfilAcessoService } from "../providers/database/services/perfil-acesso";
import { ComponentsModule } from "../components/components.module";

// AF2 Settings
export const firebaseConfig = {
  apiKey: "AIzaSyA3winfnztWrbW_k5U7PgRzOCldhPsXSDg",
  authDomain: "aguamineral-f4e5f.firebaseapp.com",
  databaseURL: "https://aguamineral-f4e5f.firebaseio.com",
  projectId: "aguamineral-f4e5f",
  storageBucket: "aguamineral-f4e5f.appspot.com",
  messagingSenderId: "259485126796"
};

// The translate loader needs to know where to load i18n files
// in Ionic's static asset pipeline.
export function HttpLoaderFactory(http: Http) {
  return new TranslateHttpLoader(http, './assets/i18n/', '.json');
}

export function provideSettings(storage: Storage) {
  /**
   * The Settings provider takes a set of default settings for your app.
   *
   * You can add new settings options at any time. Once the settings are saved,
   * these values will not overwrite the saved values (this can be done manually if desired).
   */
  return new Settings(storage, {
    option1: true,
    option2: 'Ionitron J. Framework',
    option3: '3',
    option4: 'Hello'
  });
}

@NgModule({
  declarations: [
    MyApp
  ],
  imports: [
    BrowserModule,
    HttpModule,
    TranslateModule.forRoot({
      loader: {
        provide: TranslateLoader,
        useFactory: HttpLoaderFactory,
        deps: [Http]
      }
    }),
    IonicModule.forRoot(MyApp, {}, 
{
  links: [
    { loadChildren: '../pages/autenticacao/login/login.module#LoginPageModule', name: 'LoginPage', segment: 'login', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/autenticacao/resetpwd/resetpwd.module#ResetpwdPageModule', name: 'ResetpwdPage', segment: 'resetpwd', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/autenticacao/signup/signup.module#SignupPageModule', name: 'SignupPage', segment: 'signup', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/cards/cards.module#CardsPageModule', name: 'CardsPage', segment: 'cards', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/content/content.module#ContentPageModule', name: 'ContentPage', segment: 'content', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/distribuidor/distribuidor-edit/distribuidor-edit.module#DistribuidorEditPageModule', name: 'DistribuidorEditPage', segment: 'distribuidor-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/distribuidor/distribuidor-lista/distribuidor-lista.module#DistribuidorListaPageModule', name: 'DistribuidorListaPage', segment: 'distribuidor-lista', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/geral/photo-cropper/photo-cropper.module#PhotoCropperPageModule', name: 'PhotoCropperPage', segment: 'photo-cropper', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-create/item-create.module#ItemCreatePageModule', name: 'ItemCreatePage', segment: 'item-create', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/item-detail/item-detail.module#ItemDetailPageModule', name: 'ItemDetailPage', segment: 'item-detail', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/list-master/list-master.module#ListMasterPageModule', name: 'ListMasterPage', segment: 'list-master', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/map/map.module#MapPageModule', name: 'MapPage', segment: 'map', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/menu/menu.module#MenuPageModule', name: 'MenuPage', segment: 'menu', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/painel-controle-adm.module#PainelControleAdmPageModule', name: 'PainelControleAdmPage', segment: 'painel-controle-adm', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/acao/acao-edit/acao-edit.module#AcaoEditPageModule', name: 'AcaoEditPage', segment: 'acao-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/acao/acao-list/acao-list.module#AcaoListPageModule', name: 'AcaoListPage', segment: 'acao-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/funcionalidade/funcionalidade-edit/funcionalidade-edit.module#FuncionalidadeEditPageModule', name: 'FuncionalidadeEditPage', segment: 'funcionalidade-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/funcionalidade/funcionalidade-list/funcionalidade-list.module#FuncionalidadeListPageModule', name: 'FuncionalidadeListPage', segment: 'funcionalidade-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/perfil/perfil-edit/perfil-edit.module#PerfilEditPageModule', name: 'PerfilEditPage', segment: 'perfil-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/painel-controle-adm/painel-controle-adm/perfil/perfil-list/perfil-list.module#PerfilListPageModule', name: 'PerfilListPage', segment: 'perfil-list', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/search/search.module#SearchPageModule', name: 'SearchPage', segment: 'search', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/settings/settings.module#SettingsPageModule', name: 'SettingsPage', segment: 'settings', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tabs/tabs.module#TabsPageModule', name: 'TabsPage', segment: 'tabs', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/tutorial/tutorial.module#TutorialPageModule', name: 'TutorialPage', segment: 'tutorial', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/usuario/usuario-edit/usuario-edit.module#UsuarioEditPageModule', name: 'UsuarioEditPage', segment: 'usuario-edit', priority: 'low', defaultHistory: [] },
    { loadChildren: '../pages/welcome/welcome.module#WelcomePageModule', name: 'WelcomePage', segment: 'welcome', priority: 'low', defaultHistory: [] }
  ]
}),
    IonicStorageModule.forRoot(),
    AngularFireModule.initializeApp(firebaseConfig),
    AngularFireDatabaseModule,
    AngularFireAuthModule,
    FacebookModule.forRoot(),
    DirectivesModule,
    ImageCropperModule,
    ComponentsModule    
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    MyApp
  ],
  providers: [
    Api,
    Items,
    User,
    Camera,
    GoogleMaps,
    SplashScreen,
    StatusBar,
    { provide: Settings, useFactory: provideSettings, deps: [Storage] },
    // Keep this to enable Ionic's runtime error handling during development
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    AuthServiceProvider,
    Facebook,
    DropdownService,
    ConsultaCepService,
    MaskShared,
    Geolocation,
    NativeGeocoder,
    GoogleApis,
    PhotoProvider,
    UsuarioService,
    DistribuidorService,
    MarcaService,
    PerfilAcessoService
  ]
})
export class AppModule { }



